import React, {Fragment} from 'react';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import {useTranslation} from "react-i18next";
import { backendUrl } from '../../../config/config';

import swip from '../../../assets/page/Main/swip.svg';
import './index.scss';
import {useCustomStore} from "../../../context/useStore";
import {ACTIONS} from "../../../context/providerContext";

const MainSlider = (props) => {
    const {dispatch} = useCustomStore();

    const {t} = useTranslation();
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 4000
    };
    return (
        <div className={'main-slider'}>
            <Slider {...settings}
                className={'slider-block'}
            >
                {
                    props.slider.map((el, idx) =>
                   <Fragment key={idx}>
                       <div

                           className={'list'}
                       >
                           <div className={'list_left'}>
                               <div className={'absolute'}>
                                   <h2>
                                       {el.label}
                                   </h2>
                                   <p>
                                       Вес {el.product.weight} г.
                                   </p>
                                   <span style={{whiteSpace: 'pre-line', wordBreak: 'break-all'}}>
                                    {el.product.description_ru}
                                </span>
                                   <div className={'absolute_footer'}>
                                       <button
                                          onClick={() => dispatch({type: ACTIONS.ADD_TO_BASKET, payload: el.product})}
                                       >
                                           {t('button.toOrder')}
                                       </button>
                                       <div className={'right'}>
                                                <span>
                                                    {el.product.price * (100 - el.product.percent)/100} грн.
                                                </span>
                                                <h2>{el.product.price} грн.</h2>
                                       </div>
                                   </div>

                               </div>
                           </div>
                           <div className={'list_right'}>
                                <img src={backendUrl + '/' + el.product.image} alt=""/>
                           </div>
                       </div>
                        <span className="dots">
                        <img src={swip} alt=""/>
                        </span>
                   </Fragment>
                        )

                }
            </Slider>
        </div>
    );
}

export default MainSlider;