import React from 'react';
import {Link} from "react-router-dom";
import Truncate from 'react-truncate';
import { backendUrl } from '../../config/config';
import { ACTIONS} from '../../context/providerContext';
import {useCustomStore} from "../../context/useStore";
import LazyLoad from 'react-lazy-load';

import './index.scss';

import Basket from '../Basket';

import ErrorBoundary from "../Errors/ErrorBoundary";
import { Icon } from 'react-icons-kit'
import {ic_favorite} from 'react-icons-kit/md/ic_favorite'
import {ic_favorite_border} from 'react-icons-kit/md/ic_favorite_border'

const CustomFoodBlock = React.memo((props) => {
    const {state, dispatch} = useCustomStore();

    return (
        <ErrorBoundary>
            <div
                className={'container'}
                style={{marginBottom: props.margin, width : props.width ? '48%' : 'auto'}}
            >
                <div 
                    className={props.style ? 'container_list' : 'container_block'}
                    style={props.margins ? {margin: '0 10px'} : null}
                >
                {   
                props.food.filter && 
                    <div className="triangle-for-filter-label">
                        {props.food.filter.name_ru}
                    </div>
                }
                        <div 
                        className={'list_header'} 
                        style={{
                            cursor: 'pointer'
                        }}
                        onClick={() => {
                            props.history
                                .push(props.match.path.slice(0, 3) + `/product/${props.food.categoryId.route}/${props.food.route}`)
                        }}
                        >
                                <LazyLoad
                                debounce={false}
                                offsetVertical={500}
                                offsetHorizontal={1000}>
                                    <img
                                        src={backendUrl + `/${props.food.image}`}
                                        alt={props.food.alt}
                                    />
                                </LazyLoad>
                        </div>
                    <div className={'list_footer'}>
                        <div className={'header'}>
                            <Link
                                to={props.match.path.slice(0, 3) + `/product/${props.food.categoryId.route}/${props.food.route}`}
                            >
                                <h2>
                                    {props.food.name_ru}
                                </h2>
                            </Link>
                            <div style={{  wordWrap: 'break-word' }}>
                                <Truncate
                                    lines={props.food.name_ru.legnth >= 20 ? 3 : 4}
                                    ellipsis={<span>...</span>}
                                >
                                    <span>{props.food.description_ru}</span>
                                </Truncate>
                            </div>
                        </div>
                        <div className={'center'}>
                            <div>
                                {props.food.percent !== 0 && 
                                <p>
                                    {props.food.price}грн.
                                </p>}
                                <h2>
                                    {(props.food.price * (100 - props.food.percent)/100).toFixed(0)} грн.
                                </h2>
                            </div>
                            <span>
                                <p>{props.food.weight} г.</p>
                            </span>
                        </div>
                        <div className={'footer'}>
                            <div className={'footer_left'}>
                                <button 
                                    onClick={() => dispatch({type: ACTIONS.REMOVE_TO_BASKET, payload: props.food._id})}
                                >
                                    {'-'}
                                </button>
                                <span>
                                            <p>{
                                                state.cart.basket!== undefined &&
                                                state.cart.basket.find(el => el.id === props.food._id) ?
                                                    state.cart.basket.map(el => {
                                                        if(el.id === props.food._id){
                                                            return el.countBasket
                                                        }
                                                    }) : 1
                                            }</p>
                                        </span>
                                <button 
                                    onClick={() =>  dispatch({type: ACTIONS.ADD_TO_BASKET, payload: props.food})}
                                >
                                    {'+'}
                                </button>
                            </div>
                            <Icon
                                icon={state.favourite.yourFavourite.find(el => el._id === props.food._id) ? ic_favorite : ic_favorite_border}
                                size={(window.innerWidth < 610) ? 25 : 30}
                                style={{color: '#43A948'}}
                                onClick={() => dispatch({type: ACTIONS.ADD_TO_FAVOURITE, payload: props.food})}
                            />
                            <Basket {...props} style={'open-modal'} title={'Заказать'} />
                        </div>
                    </div>
                </div>
            </div>
        </ErrorBoundary>
    )
});

export default CustomFoodBlock;